*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
#root{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 500px;
    border: 0px solid;
}
.main--container{
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    gap:50px;
}
.die--container{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid;
    width: 75px;
    height: 75px;
    border-radius: 5px;
    cursor: pointer;
}
.roll--btn{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    border: 0px;
    background-color: blue;
    padding: 20px;
    width: auto;
    height: 50px;
    border-radius: 5px;
    cursor: pointer;
    color: white;
}
.isHeld{
    color: green;
}